*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-size: 'Segoe UI', Tahoma, Geneva, Verdana
    sans-serif;
}

.containers{
    max-width: 700px;
    width: 100%;
    padding: 25px 30px;
    border-radius: 25px;
    background: linear-gradient(135deg, #dc0606ca, #05073a
    );
}
.containers .header{
    font-size: 25px;
    font-weight: 500px;
    position: relative;
    text-align: center;
    padding: 0 0 20px 0;
}
.containers .header::before{
    content:'';
    position: absolute;
    height: 3px;
    left: 0;
    bottom: 0;
    width: 100%;
    background: linear-gradient(135deg, #71b7e6,
    #9b5b)
}
.containers form .card-details{
    margin-top: 25px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
form .card-details .card-box{
    width: calc(100% / 2 - 20px);
    margin-bottom: 15px;
}
.card-details .card-box .details{
    display: block;
    font-weight: 500;
    margin-bottom: 5px
}
.card-details .card-box input{
    height: 45px;
    width: 100%;
    outline: none;
    border-radius: 5px;
    border: 1px solid #ccc;
    padding-left: 15px;
    font-size: 16px;
    border-bottom-width: 2px;
    transition: all 0.3s ease;
}
.card-details .card-box input:focus,
.card-details .card-box input:valid{
    border-color: #9b59b6;
}
form .button{
    text-align: center;
}
form .button input{
    padding: 10px 0;
    margin-top: 10px;
    height: 100%;
    width: 50%;
    outline: none;
    color: #fff;
    border: none;
    font-size: 18px;
    font-weight: 500;
    border-radius: 5px;
    letter-spacing: 1px;
    background: linear-gradient(135deg, #05073a,
    #dc0606ca);
}
form .button input:hover{
    background: linear-gradient(-135deg, #05073a,
    #dc0606ca);
}




@media (max-width:584px){
    .containers{
        max-width: 100px;
    }
    form .card-details .card-box{
        margin-bottom: 15px;
        width: 100%;
    }
    .containers form .card-details{
        max-width: 300px;
        overflow: scroll;
    }
.card-details::-webkit-scrollbar{
    width: 0;
}

}
